import { Navigate, useRoutes } from 'react-router-dom';
// layouts
import DashboardLayout from './layouts/dashboard';
import LogoOnlyLayout from './layouts/LogoOnlyLayout';
//
import Login from './pages/Login';
import Register from './pages/Register';
import DashboardApp from './pages/DashboardApp';
import Agendas from './pages/Agendas';
import Polls from './pages/Polls';
import Attendees from './pages/Attendees';
import NotFound from './pages/Page404';
import Notification from './pages/Notification';
import BulkMail from './pages/Bulkmail';
import Pressroom from './pages/Pressroom';
import Informations from './pages/Informations';

// ----------------------------------------------------------------------

export default function Router() {
  return useRoutes([
    {
      path: '/dashboard',
      element: <DashboardLayout />,
      children: [
        { element: <Navigate to="/dashboard/app" replace /> },
        { path: '/mini-admin/app', element: <DashboardApp /> },
        { path: '/mini-admin/attendees', element: <Attendees /> },
        { path: '/mini-admin/agendas', element: <Agendas /> },
        { path: '/mini-admin/polls', element: <Polls /> },
        { path: '/mini-admin/notification', element: <Notification /> },
        { path: '/mini-admin/bulkmail', element: <BulkMail /> },
        { path: '/mini-admin/pressroom', element: <Pressroom /> },
        { path: '/mini-admin/informations', element: <Informations /> }
      ]
    },
    {
      path: '/',
      element: <LogoOnlyLayout />,
      children: [
        { path: 'login', element: <Login /> },
        { path: 'register', element: <Register /> },
        { path: '404', element: <NotFound /> },
        { path: '/', element: <Navigate to="/dashboard" /> },
        { path: '*', element: <Navigate to="/404" /> }
      ]
    },
    { path: '*', element: <Navigate to="/404" replace /> }
  ]);
}
